.mainContainer {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 64px;
}

.dataContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 600px;
}

.header {
  text-align: center;
  font-size: 32px;
  font-weight: 600;
  color: #b7cbeb;
}

.bio {
  text-align: center;
  color: gray;
  margin-top: 16px;
}

.waveButton {
  cursor: pointer;
  margin-top: 16px;
  padding: 8px;
  border: 0;
  border-radius: 11px;
  background-color: #b7cbeb;
}

.message {
  background-color: oldlace;
  margin-top: 16px;
  padding: 8px;
  border-radius: 11px;
}

.messageInput {
  width: 100%;
}

.submitButton {
  width: 600px;
}

Label {
  color: oldlace;
}
